// КНОПКА




/* База кнопки */

@mixin button($bg: $first, $borderColor: $second) {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	padding: em(16) em(32) em(17) em(32);
	box-shadow: none;
	font-family: Manrope, sans-serif;
	font-size: rem(12);
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;
	white-space: nowrap;
	border-radius: em(4);
	border: $borderColor;
	color: $white;
	cursor: pointer;
	background: $bg;
	@content;

	@include smart {
		font-size: rem(13);
	}

}

/* ---------- */




/* Основная кнопка */

// linear-gradient(to right, #F17519 50%, #FFAF13 100%)
.main-btn {
	@include button($btn, transparent);
	transition: background-color 0.2s ease-in-out;

	.ic {
		min-width: rem(10);
		min-height: rem(10);
		max-width: rem(10);
		max-height: rem(10);
		margin-left: rem(8);
		background-image: url('../img/icons/interface/ic-arrowSquare.svg');
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;
	}

	&:hover {
		color: $white;
		background-color: $btn-hvr;
	}

	&:active {
		background-color: $btn-act;
	}
}

/* ---------- */




/* Кнопка контур */

.border-btn {
	@include button(transparent, $border-input);
	border-width: rem(1);
	border-style: solid;
	color: $white;
	transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;

	.ic {
		min-width: rem(10);
		min-height: rem(10);
		max-width: rem(10);
		max-height: rem(10);
		margin-left: rem(8);
		background-image: url('../img/icons/interface/ic-arrowSquare.svg');
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;
	}



	&:hover {
		color: $white;
		background-color: $btn-hvr;
		border-color: $btn-hvr;
	}
	
	&:active {
		background-color: $btn-act;
		border-color: $btn-act;
	}
}

/* ---------- */






