// БЛОК «REVIEWS»

/* Основа */

.b5-tr {
	background-color: $bg-light;
	@include laptop {
		background-image: url("../img/pic/bg/start-bg.jpg");
		background-repeat: no-repeat;
		background-size: cover;
	}
}

/* ---------- */

/* Название блока */

.review__card {
	display: flex;
	flex-direction: column;
	padding: rem(16);
	background-color: $card;
	border-radius: rem(10);

	@include tabletm {
		padding: rem(24);
	}
}

.review__user {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	margin-bottom: rem(16);

	.user-wrap {
		display: flex;
		flex-direction: row;
		align-items: center;

		.ava {
			width: rem(40);
			height: rem(40);
		}

		.user__name {
			display: flex;
			flex-direction: column;
			align-items: center;
			p {
				@include adap-font(16, 20);
				font-weight: 700;
				color: $white;
			}
			span {
				@include adap-font(11, 13);
			}
		}
	}

	.ic-quote {
		width: rem(32);
		height: rem(32);
		background-image: url("../img/icons/interface/ic-quote.svg");
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;
	}
}

.review__quote {
	@include text-short(4);
	p {
		@include adap-font(14, 24);
		color: $white;
	}
}

