// НАЗВАНИЕ БЛОКА

/* Основной заголовок для всех блоков */

.block-title {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(24);

	h2 {
		margin-bottom: rem(8);
	}

	p {
		@include adap-font(12, 18);
	}

	@include tabletm {
		margin-bottom: rem(40);
		align-items: center;
		text-align: center;

		h2 {
			margin-bottom: rem(24);
		}
	}

	@include tabletb {
		padding-bottom: rem(40);
		border-bottom: 1px solid $border-input;
		margin-bottom: rem(40);
	}

	@include laptop {
		padding-bottom: rem(60);
		margin-bottom: rem(60);
	}
}

/* ---------- */

/* Основа */

.b2-title {
	margin-bottom: rem(0) !important;
	h2 {
		margin-bottom: 0 !important;
	}
}

/* ---------- */

/* Блок */

.trends__title {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(24);

	@include tabletb {
		align-items: center;
		flex-direction: row;
		justify-content: space-between;
		padding-bottom: rem(40);
		border-bottom: 1px solid $border-input;
		margin-bottom: rem(40);
	}
}

.trends__title-menu {
	text-align: center;
}

.tr-tit-item {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;

	a {
		display: block;
		white-space: nowrap;
		padding-left: rem(8);
		padding-right: rem(8);
		color: $text-light2;

		&:hover {
			color: $btn-hvr;
		}
	}
}

/* ---------- */

/* Карточки */

.trends-slider {
}

.trend-card {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: rem(10);
	border: rem(1) solid $border-input2;
	border-radius: rem(8);

	.brand-logo {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: rem(4);

		.ic {
			width: rem(16);
			height: rem(16);
			margin-bottom: rem(8);

			@include tabletb {
				width: rem(32);
				height: rem(32);
			}
		}

		p {
			@include adap-font(12, 20);
		}
	}

	.brand__rate {
		display: flex;
		flex-direction: column;
		align-items: center;

		.rate-numbers {
			margin-top: rem(8);
			margin-bottom: rem(8);
			p {
				@include adap-font(11, 20);
			}
		}
	}

	.brand__graph {
		display: flex;
		justify-content: center;
	}

	@include tabletb {
		padding: rem(32) rem(16);
	}
}

.tr-btn-wr {
	display: flex;
	justify-content: center;
	margin-top: rem(24);
}

/* ---------- */

// КОНЕЦ БЛОКА ==========
