// БЛОК «ПЕРВЫЙ ЭКРАН»

/* Основа */

.banner {
	position: relative;
	&::after {
		@include laptop {
			position: absolute;
			z-index: -1;
			top: rem(-100);
			right: 0;
			content: "";
			width: rem(1036);
			height: rem(977);
			background-image: url("../img/pic/bg/circle-bg.png");
			background-repeat: no-repeat;
			background-size: contain;
			background-position: center;
		}
	}
}

.banner__wrap {
	@include row;
	margin-bottom: rem(32);

	@include smart {
		margin-bottom: rem(64);
	}

	@include tabletb {
		margin-bottom: rem(120);
	}
}

/* ---------- */

/* Блок слева */

.banner__left {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(32);
	@include col-12;

	& > * {
		margin-bottom: rem(16);

		&:last-child {
			margin-bottom: 0;
		}

		@include tabletm {
			margin-bottom: rem(32);
		}
	}

	@include smart {
		@include col-7;
		margin-bottom: 0;
	}
}

.banner__title {
	h1 {
		color: $white;
		span {
			position: relative;
			font-weight: inherit;
			color: inherit;

			&::after {
				display: none;

				@include desktop {
					display: block;
					position: absolute;
					left: 0;
					bottom: rem(-6);
					width: 100%;
					height: rem(1);
					content: "";
					background-image: url("../img/pic/bg/title-line.png");
					background-repeat: no-repeat;
					background-size: cover;
					background-position: center;
				}
			}
		}
	}
}

.banner__text {
	p {
		@include adap-font(14, 20);
	}

	@include laptop {
		max-width: rem(800);
	}
}

.banner__btns {
	display: flex;
	flex-direction: row;

	& button:first-child {
		margin-right: rem(8);
	}
}

/* ---------- */

/* Блок справа */

.banner__right {
	display: flex;
	justify-content: center;
	align-items: center;
	@include col-12;

	@include smart {
		@include col-5;
	}
}

.bn-r-wr {
	@include col-6;
	@include smart {
		@include col-12;
	}
}

/* ---------- */

/* Котировки */

.rate__mini-item {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: rem(10);

	@include laptop {
		border-right: 1px solid $border-input2;
	}
}

.rate-title {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

.rate-title-lft {
	@include adap-font(10, 16);
	text-transform: uppercase;
	margin-right: rem(8);
}

.rate-mini {
	display: flex;
	flex-direction: row;
	align-items: center;
	color: $green;
	@include adap-font(10, 14);

	.ic {
		min-width: rem(10);
		min-height: rem(10);
		max-width: rem(10);
		max-height: rem(10);
		margin-right: rem(4);
		background-image: url("../img/icons/interface/ic-arrowGrow.svg");
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;
	}
}

.rate-numbers {
	p {
		@include adap-font(14, 20);
		font-weight: 600;
		color: $white;
	}
}

/* ---------- */

/* Пагинация всех слайдеров */

.tns-nav {
	margin-top: rem(16);
	display: flex;
	flex-direction: row;
	justify-content: center;

	button {
		width: rem(24) !important;
		height: rem(2) !important;
		outline: none !important;
		background-color: #4d4f55;
		border: none;
		margin-left: rem(4);
		margin-right: rem(4);
	}
	.tns-nav-active {
		background-color: $white;
	}
}

/* ---------- */

// КОНЕЦ БЛОКА «ПЕРВЫЙ ЭКРАН» ==========
