// БЛОК «PROCESS»

/* Основа */

.b3-tr {
	background-color: $bg-light;
	@include laptop {
		background-image: url("../img/pic/bg/start-bg.jpg");
		background-repeat: no-repeat;
		background-size: cover;
	}
}

/* ---------- */

/* Название блока */

.process__steps {
	display: grid;
	grid-template-columns: repeat(1, 1fr);
	gap: rem(10);

	@include tabletm {
		grid-template-columns: repeat(2, 1fr);
	}

	@include laptop {
		grid-template-columns: repeat(4, 1fr);
	}
}

.step-item {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	padding: rem(16);
	border-radius: rem(10);
	background-color: #272a34;

	& > * {
		margin-bottom: rem(16);
		&:last-child {
			margin-bottom: 0;
		}
	}

	.ic {
		width: rem(32);
		height: rem(32);
	}

	@include tabletb {
		padding: rem(42) rem(24);
	}
}

.step-title {
	p {
		@include adap-font(16, 24);
		font-weight: 600;
		color: $white;
	}
}

.step-desc {
	p {
		@include adap-font(12, 16);
	}
}

.stp-btn {
	font-size: rem(9);
}

