// ПОДВАЛ

/* Основной блок */

.footer {
	display: flex;
	justify-content: center;
	flex-direction: column;
	background-color: $bg-light;

	@include laptop {
		background-image: url("../img/pic/bg/start-bg.jpg");
		background-repeat: no-repeat;
		background-size: cover;
	}
}

/* ---------- */

/* Первая строчка футера */

.footer__top {
	display: flex;
	flex-direction: column;
	padding-top: rem(32);
	padding-bottom: rem(32);
	border-bottom: 1px solid $border-input;

	.logo-company {
		margin-bottom: rem(16);

		@include tabletm {
			margin-bottom: 0;
		}
	}

	@include tabletm {
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
	}
}

.footer__social {
	display: flex;
	flex-direction: row;
	align-items: center;

	p {
		display: none;
		color: $white;
		@include tabletm {
			display: initial;
			margin-right: rem(16);
		}
	}

	.social__col {
		display: flex;
		flex-direction: row;
		align-items: center;

		& > * {
			margin-right: rem(8);
			&:last-child {
				margin-right: 0;
			}
		}

		a {
			display: block;
			display: flex;
			align-items: center;
			justify-content: center;
			width: rem(24);
			height: rem(24);

			img {
				transition: opacity 0.2s ease-in-out;
			}
			&:hover {
				img {
					opacity: 0.5;
				}
			}
		}
	}
}

/* ---------- */

/* Середина */

.footer__menu {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: rem(16);
	padding-top: rem(32);
	padding-bottom: rem(32);
	border-bottom: 1px solid $border-input;

	@include tabletm {
		grid-template-columns: repeat(3, 1fr);
		gap: rem(24);
	}

	@include tabletb {
		grid-template-columns: repeat(6, 1fr);
	}
}

.menu-col {
	ul {
		& > * {
			margin-bottom: rem(8);
			&:last-child {
				margin-bottom: 0;
			}

			@include laptop {
				margin-bottom: rem(16);
			}
		}
	}

	a {
		color: $text-light1;
		@include adap-font(14, 16);
		&:hover {
			color: $btn-hvr;
		}
	}
}

.f-menu-col {
	@include adap-font(14, 18);
	font-weight: 600;
	color: $white;
}

/* ---------- */

/* Низ */

.footer__bottom {
	padding-top: rem(24);
	padding-bottom: rem(24);
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	text-align: center;
	justify-content: center;

	p,
	a {
		font-size: rem(13);
		color: $white;
		padding: rem(6);
	}

	a {
		&:hover {
			color: $btn-hvr;
		}
	}
}

