// БЛОК «ПРИЛОЖЕНИЯ»

.b6pdtb {
	@include desktop {
		padding-top: rem(240);
		padding-bottom: rem(140);
	}
}

/* Основа */

.b6-title {
	align-items: flex-start !important;
	text-align: left !important;
	border-bottom: 0 !important;
	margin-bottom: 0 !important;
}

.b6-tr {
	overflow: hidden;
}

.app__left {
	margin-bottom: rem(32);

	@include tabletm {
		margin-bottom: 0;
		@include col-8;
	}

	@include desktop {
		@include col-6;
	}
}

.app__btns {
	display: flex;
	flex-direction: row;
	margin-top: rem(16);
	& > * {
		margin-right: rem(16);
		&:last-child {
			margin-right: 0;
		}
	}
}

.app-btn {
	display: flex;
	flex-direction: column;
	font-size: rem(8);

	.ic-app {
		min-width: rem(16);
		min-height: rem(16);
		max-width: rem(16);
		max-height: rem(16);
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;

		@include tabletm {
			margin-right: rem(8);
		}
	}

	@include tabletm {
		flex-direction: row;
		align-items: center;
	}
}

.appStore {
	.ic-app {
		background-image: url("../img/icons/apps/ic-appst.svg");
	}
}
.playStore {
	.ic-app {
		background-image: url("../img/icons/apps/ic-play.svg");
	}
}
.qrCode {
	.ic-app {
		min-width: rem(24);
		min-height: rem(24);
		max-width: rem(24);
		max-height: rem(24);
		background-image: url("../img/icons/apps/ic-code.svg");
		margin: 0 !important;
	}
}

/* ---------- */

/* Название блока */

.app__right {
	display: flex;
	align-items: center;

	@include tabletm {
		@include col-4;
	}

	@include desktop {
		@include col-6;
	}
}

.app-pic-wrap {
	position: relative;
	height: calc(100% - 20%);

	@include tabletm {
		img {
			max-width: rem(600);
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
		}
	}

	@include desktop {
		img {
			max-width: rem(1130);
			height: auto;
		}
	}
}

