// БЛОК «ПОЧЕМУ МЫ»

/* Основа */

.b4-tr {
	position: relative;

	&::after {
		position: absolute;
		z-index: -1;
		top: 0;
		right: 0;
		content: "";
		width: rem(1920);
		height: rem(919);
		background-image: url("../img/pic/bg/el-bg.png");
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;
		opacity: 0.5;
	}
}

.choose__wrap {
	display: flex;
	flex-direction: column;

	@include tabletb {
		flex-direction: row;
	}
}

/* ---------- */

/* Название блока */

.choose__left {
	@include laptop {
		@include col-6;
	}
}

.choose__reasons {
	display: flex;
	flex-direction: column;

	& > * {
		padding-top: rem(24);
		padding-bottom: rem(24);
		border-bottom: 1px solid $border-input2;

		&:last-child {
			border: none;
		}
	}

	@include tabletb {
		margin-right: rem(32);
	}
}

.reason__item {
	display: flex;
	flex-direction: column;
	@include tabletm {
		flex-direction: row;
		align-items: center;
	}
}

.reason__info {
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
	align-items: flex-start;
	margin-bottom: rem(16);

	.num {
		padding: rem(6);

		span {
			@include adap-font(24, 56);
			color: #272a34;
			line-height: 100%;
		}

		@include tabletm {
			padding: rem(16);
			padding-left: 0;
		}
	}

	@include tabletm {
		flex-direction: row;
		align-items: center;
		margin-right: rem(24);
		margin-bottom: 0;
	}
}

.reason__text {
	display: flex;
	flex-direction: column;
}

.reason__title {
	margin-bottom: rem(8);
	p {
		@include adap-font(20, 24);
		color: $white2;
		font-weight: 600;
	}
}

.reason__desc {
	p {
		@include adap-font(13, 16);
	}
}

.rsn-btn {
	width: auto;
}

/* ---------- */

/* Название блока 2 */

.choose__right {
	display: flex;
	justify-content: center;

	.ch-pic-wr {
		max-width: rem(400);
		@include laptop {
			max-width: rem(781);
			height: auto;
		}
	}

	@include laptop {
		@include col-6;
	}
}

/* ---------- */

// КОНЕЦ БЛОКА «ПОЧЕМУ МЫ» ==========
