/* Карточки вопросов */

.faq__items {
	display: grid;
	grid-template-columns: repeat(1, 1fr);
	gap: rem(10);

	@include laptop {
		grid-template-columns: repeat(2, 1fr);
	}
}

.faq__item {
	display: flex;
	flex-direction: column;
	justify-content: center;
	border: 1px solid $border-input;
	padding: rem(8);
	border-radius: rem(4);
	transition: background-color 0.2s ease-in-out;

	&:hover {
		background-color: $border-input;
		cursor: pointer;
	}

	@include tabletm {
		padding: rem(16);
	}
}

.faq-title {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;

	p {
		@include adap-font(14, 22);
		font-weight: 600;
		color: $white;
		margin-right: rem(8);
	}

	.ic {
		display: block;
		position: relative;
		min-width: rem(16);
		min-height: rem(16);
		max-width: rem(16);
		max-height: rem(16);
		background-image: url("../img/icons/interface/ic-plus.svg");
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;
	}
}

.ic-rot {
	transform: rotate(225deg);
}

.faq-text {
	display: none;
	p {
		@include adap-font(12, 16);
		margin-top: rem(8);
	}
}

.f-t-open {
	display: flex !important;
}

