// КОНТАКТЫ



/* Простые контакты */

.phone-simp,
.mail-simp,
.address-simp {

	display: flex;
	align-items: center;

	.ic {
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: rem(20);
		min-height: rem(20);
		max-width: rem(20);
		max-height: rem(20);
		margin-right: rem(8);

		svg {
			fill: $main-color;
		}
	}

}

.phone-simp {

	a {
		font-size: rem(22);
		color: $title;
		white-space: nowrap;
		
		&:hover {
			color: $btn;
			
			&:before {
				background-color: $btn;
			}
		}
	}
}

.mail-simp {

	a {
		@include adap-font(14, 16);
	}
}

.address-simp {

	p {
		@include adap-font(13, 14);
	}
	
}


