// ИНПУТЫ

/* База ИНПУТОВ */

@mixin input($bg: $first, $borderColor: $second) {
	position: relative;
	display: block;
	width: 100%;
	padding: em(16);
	border-radius: rem(4);
	border: rem(1) solid $borderColor;
	font-family: 'Open Sans', Roboto, sans-serif;
	font-size: rem(16);
	appearance: none;
	cursor: text;
	outline: none;
	box-shadow: none;
	line-height: normal;
	background-color: $bg;
	color: $white;
    transition: background-color .15s ease-in-out, border-color .15s ease-in-out;
	
	&::placeholder {
		transition: color 0.2s ease-in-out;
		color: $text-light2;
	}

	@content;

}

/* ---------- */




/* Вид основных инпутов */

input[type=text],
input[type=email],
input[type=tel],
input[type=password] {
	@include input($input-bg, $input-bg);
	position: relative;

	&:hover {
		border: rem(1) solid lighten($btn, 35%);
		&::placeholder {
			color: $white;
		}
	}

	&:focus {
		border: rem(1) solid $btn;
		&::placeholder {
			color: $white;
		}
	}

}

.input-box {
	position: relative;
	text-align: right;
	width: 100%;
}

